@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ranchers&display=swap);
nav {
  background-color: transparent; 
  -webkit-backdrop-filter: blur(10px); 
          backdrop-filter: blur(10px);
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;  
  align-items: center;
  justify-content: space-between;
  padding: 0 3rem;
  transition: .35s;
}
nav #logo {
  font-family: 'Major Mono Display', monospace;
  color: var(--brown);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
nav ul {
  display: flex;
}
nav ul li {
  list-style: none;
}
nav li a {
  color: var(--white);
  padding: 0 1.25rem;
  font-size: var(--fz-md);
  font-weight: bold;
  transition: .35s;
}
nav li a:hover {
  color: var(--orange);
}
nav .right-nav {
  display: flex;
  align-items: center;
}

nav .menu-btn {
  display: none;
  transition: all 0.4s ease-out;
}
nav .btn-line {
  width: 25px;
  height: 3px;
  background-color: #3b3b3b;
  transition: all 0.25s ease-out;
}
nav .btn-line:nth-child(2) {
  width: 20px;
  margin-top: 6px;
  margin-bottom: 6px;
}
nav .menu-btn.close .btn-line:nth-child(1) {
  -webkit-transform: rotate(45deg) translate(6px, 6px);
          transform: rotate(45deg) translate(6px, 6px);
}
nav .menu-btn.close .btn-line:nth-child(2) {
  opacity: 0;
}
nav .menu-btn.close .btn-line:nth-child(3) {
  -webkit-transform: rotate(-45deg) translate(7px, -7px);
          transform: rotate(-45deg) translate(7px, -7px);
}


/*** --- Media Queries --- ***/
/* Medium Dimension */
@media only screen and (max-width: 768px) {
  nav .right-nav,
  nav .right-nav + button {
    display: none;
  }
  nav .menu-btn {
    display: block;
  }
  nav, main {
    padding: 0 2rem;
  }
}
.home-content {
  display: flex;
  flex-direction: column;
  grid-gap: .25rem;
  gap: .25rem;
}

#home .my-name {
  font-family: 'Ranchers', cursive;
  font-size: 3rem;
  font-weight: bold;
  letter-spacing: 1px;
  word-spacing: 5px;
}

#home a {
  color: #352F2F;
  transition: 0.25s;
}
#home a:hover {
  color: #c73d0f;
}

#home .contact-details {
  display: flex;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
  margin-top: 1rem;
}

#home .contact-details p:nth-child(odd) {
  font-weight: bold;
}

#home .social-connect {
  list-style: none;
  display: flex;
  margin-top: 1rem;
  border: 1px solid #352F2F;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 5px;
}
#home .social-connect li {
  padding: .5rem 1rem;
}
#home .social-connect a {
  font-size: 1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

/*** --- Media Queries --- ***/
/* Mobile Dimension */
@media only screen and (max-width: 480px) {
  #home .contact-details {
    display: flex;
    flex-direction: column;
    grid-row-gap: 1rem;
    row-gap: 1rem;
  }
}

*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --brown: #352F2F;
  --light-brown: #DEC6A2;
}

::selection {
  background-color: rgba(53, 47, 47, .75);
  color: #DEC6A2;
}

::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-track {
  background:  #352F2F;
  background:  var(--brown);
}
::-webkit-scrollbar-thumb {
  background: #DEC6A2;
  background: var(--light-brown); 
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #DEC6A2;
  background-color: var(--light-brown);
  color: #352F2F;
  color: var(--brown);
  font-family: 'Source Sans Pro', sans-serif;
}

a {
  text-decoration: none;
}

button {
  background-color: #DEC6A2;
  background-color: var(--light-brown);
  color: #352F2F;
  color: var(--brown);
  z-index: 1;
  position: relative;
  padding: 0.65rem 1.2rem;
  font-weight: bold;
  font-family: inherit;
  border: 2px solid #352F2F;
  border: 2px solid var(--brown);
  outline: none;
}
button::before {
  content: '';
  z-index: -1;
  position: absolute;
  top: -1px;
  bottom: -1px;
  left: 0;
  right: 0;
  background-color: #352F2F;
  background-color: var(--brown);
  -webkit-transform-origin: center right;
          transform-origin: center right;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: -webkit-transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
}
button:hover {
  cursor: pointer;
  color: #DEC6A2;
  color: var(--light-brown);
}
button:hover::before {
  -webkit-transform-origin: center left;
          transform-origin: center left;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

main {
  margin: 0 auto;
  width: 100%;
  max-width: 100rem;
  min-height: 100vh;
  padding: 0 12.5rem;
}

section {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  max-width: 62.5rem;
  min-height: 100vh;
}


/*** Media Queries ***/
@media only screen and (max-width: 768px) {
  nav, main {
    padding: 0 2rem;
  }
}


